/*=====================
    32.Dark css start
==========================*/

body {
    &.dark {
        background-color: $dark-body;
        transition: all 0.3s ease;
        color: $white-6;
        background: $dark-body;
    }
}

.dark {
    h1 {
        color: $white-1;
    }
    h2 {
        color: $white-2;
    }
    h3 {
        color: $white-3;
    }
    h4 {
        color: $white-4;
    }
    h5 {
        color: $white-5;
    }
    h6 {
        color: $white-6;
    }
    p {
        color: $white-6;
    }
    li {
        color: $white-6;
    }
    a {
        color: $dark-link;
    }
    .bg-inner {
        background-color: $dark-main;
    }

}

.dark {
    header .main-navbar .nav-menu > li .nav-submenu {
        background: #2b2b2b;
    }
    .classic-section .classic-box .top-part span {
    color: rgba(255,255,255,0.7);
}
    .title-3.detail-title p {
        color: #cfd4da;
    }
    .service-part .service-wrapper h6{
        color:  #fff;   
    }
    .tourSection .tourBox .tourContent h3 { 
    color: black;
}
.blog_section.destination-section .destination-details h6{
        color: rgba(255,255,255,0.56);
}
.layout-3{
    .tourSection .tourBox .tourContent h3{
        color: #fff;
    }
}
.mix-2{
    .tourSection .tourBox .tourContent h3{
        color: white;
    }    
}
    .category-sec {
        .category-box {
            .content-category {
                .top {
                    h3 {
                        color: #fff;
                    }
                }
            }
        }
    }
    .btn {
        &.btn-rounded {
            &.color1 {
                box-shadow: 2.783px 19.805px 20px 0px rgba(0, 0, 0, 0.22);
                &:hover {
                    box-shadow: 2.783px 19.805px 20px 0px rgba(0, 0, 0, 0.22);
                }
            }
        }
        &.btn-solid {
            box-shadow: 2.783px 19.805px 20px 0 rgba(0, 0, 0, 0.12);
        }
    }
    .bg-white {
        background-color: $dark-body !important;
    }
    .dark-cls {
        background-blend-mode: overlay;
        background-color: #191919;
    }
    header {
        &.light_header {
            background-color: $dark-body;
            box-shadow: none;
            .main-navbar {
                .nav-menu {
                    >li {
                        >a {
                            color: $dark-link;
                        }
                        .nav-submenu {
                            background-color: $dark-body;
                        }
                    }
                }
            }
            .menu {
                .header-right {
                    .user {
                        &.user-light {
                            background-color: white;
                        }
                    }
                    .front-setting {
                        background-color: rgba(white, 0.14);
                        select {
                            color: white;
                            background: url(../images/icon/down.png) no-repeat 80%;
                            option {
                                background-color: $dark-body;
                                color: $white-6;
                            }
                        }
                    }
                    .setting {
                        background-color: rgba(255, 255, 255, 0.14);
                        i {
                            color: white;
                        }
                    }
                }
                .brand-logo {
                    img {
                        filter: invert(0);
                    }
                }
            }
            .toggle-nav {
                i {
                    color: white;
                }
            }
        }
        .main-navbar {
            .nav-menu {
                >li {
                    .nav-submenu {
                        li {
                            a {
                                color: $dark-link;
                            }
                            .nav-sub-childmenu {
                                background: $dark-body;
                            }
                        }
                    }
                    .mega-menu-container {
                        background: $dark-body;
                        .mega-box {
                            .link-section {
                                .submenu-content {
                                    ul {
                                        li {
                                            a {
                                                color: $dark-link;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .menu {
            .header-right {
                .setting {
                    .setting-open {
                        background-color: $dark-body;
                    }
                }
            }
        }
        .mix-pills {
            &.nav-pills {
                .nav-link {
                    color: white;
                    svg {
                        fill: white;
                    }
                }
            }
            &.nav-pills {
                .nav-link.active,
                .show>.nav-link {
                    color: $theme-color1;
                    svg {
                        fill: $theme-color1;
                    }
                }
            }
        }
    }
    .about-section.three-image {
        .about-text {
            h2 {
                span {
                    color: rgba(255, 255, 255, 0.6);
                }
            }
        }
    }
    .home_section {
        .home {
            .home-content {
                &.mix-layout {
                    &.smaller-content {
                        .search-panel {
                            .search-section {
                                &.search-fixed {
                                    background-color: $dark-body;
                                    box-shadow: 0 0 8px #191919;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .filter-none {
        filter: invert(0) !important;
    }
    footer {
        &.footer-light {
            .footer {
                background-color: rgba(black, 0.15);
                .footer-content {
                    .contact-detail {
                        p {
                            color: rgba(white, 0.6);
                        }
                        .contact-list {
                            li {
                                color: white;
                            }
                        }
                        .footer-logo {
                            img {
                                filter: unset;
                            }
                        }
                    }
                    .footer-links {
                        ul {
                            li {
                                a {
                                    color: white;
                                }
                            }
                        }
                    }
                    .footer-blog {
                        .media {
                            .media-body {
                                h5 {
                                    color: $white-5;
                                }
                                p {
                                    color: rgba(white, 0.6);
                                }
                            }
                        }
                    }
                }
                .footer-title {
                    h5 {
                        color: $white-5;
                    }
                }
            }
            .sub-footer {
                background-color: rgba(0, 0, 0, 0.3);
            }
        }
    }
    .form-control {
        color: $dark-link !important;
    }
    .title-1 {
        &.detail-title {
            p {
                color: rgba(white, 0.41);
            }
        }
    }
    .highlight-section {
        .highlight-box {
            svg {
                fill: rgba(white, 0.67);
            }
            .content-sec {
                p {
                    color: rgba(white, 0.38);
                }
            }
        }
    }
    .car-type-section {
        .type-box {
            box-shadow: 1px 2px 39px 9px rgba($dark-main, 0.6);
            .content {
                p {
                    color: rgba(white, 0.6);
                }
                ul {
                    li {
                        img {
                            filter: invert(1);
                        }
                    }
                }
            }
        }
    }
    .process-steps {
        .step-bg {
            .step-box {
                background-color: $dark-body;
                img {
                    filter: invert(1);
                }
            }
            h4 {
                color: rgba(white, 0.65);
            }
        }
        .title-1 {
            &.detail-title {
                h2 {
                    color: black;
                }
                p {
                    &.text-dark {
                        color: black !important;
                    }
                }
            }
        }
    }
    .success-section {
        .success-detail {
            p {
                color: $white-6;
            }
        }
    }
    .form-icon{
        .form-group{
            .icon{
                color: rgba(255, 255, 255, 0.5);
            }
        }
    }
    .category-part{
        .category-block{
            .category-details{
                h3 {
                    color: white; 
                }
                h6 {    
                    color: rgba(255, 255, 255, 0.7);
                }
            }
        }
    }
    

    .process-steps{
        .step-bg{
           .popular-section{
            .step-box{
               .popular-box{
                   .special-content{
                    .bottom-section .price del { 
                        color: white; 
                    }
                    h5{
                        color: white;
                    }
                }
            }
        }
    }
}
}
.highlight-section{
    .highlight-box { 
        border: 1px solid #3a3939;    
    }
}

.team-section,
.facts-section {
    background-blend-mode: overlay;
    background-color: $dark-main;
}
.about-section {
    &.three-image {
        .about-text {
            h2 {
                color: white;
            }
            img {
                filter: invert(1);
            }
        }
    }
    &.about_page {
        .image-section {
            .img-box {
                .title-box {
                    background-color: $dark-main;
                    h3 {
                        color: $white-3;
                    }
                    h6 {
                        color: $white-6;
                    }
                }
            }
        }
    }
}
.team-section {
    .team-box {
        .team-content {
            background-color: $dark-body;
            h3 {
                color: $white-3;
            }
        }
        &:hover {
            .team-social {
                .social-box {
                    background-color: $dark-body;
                }
            }
        }
    }
}
.facts-section {
    .facts-box {
        background-color: $dark-body;
        h3 {
            color: $white-3;
        }
    }
}
.blog_section {
    .blog-wrap {
        background-color: $dark-main;
        .blog-details {
            h6 {
                color: $white-6;
            }
            h5 {
                color: $white-5;
            }
            p {
                color: $white-6;
            }
        }
        .blog-image {
            .blog-label {
                background-color: $dark-body;
                h3 {
                    color: $white-3;
                }
                h6 {
                    color: $white-6;
                }
            }
        }
    }
    &.destination-section {
        .destination-details {
            h2 {
                color: $white-2;
            }
        }
    }
}
.testimonial-section {
    .testimonial {
        .right-part {
            p {
                color: $white-6;
            }
            .detail {
                h6 {
                    color: $white-6;
                }
            }
        }
    }
}
.testimonial_section {
    .testimonial {
        .bottom-part {
            p {
                color: $white-6;
            }
        }
    }
}
.arrow-classic {
    .slick-next,
    .slick-prev {
        &:before {
            background-color: $dark-main;
        }
    }
}
.service-section {
    .service-box {
        background-color: $dark-body;
        box-shadow: none;
        h3 {
            color: $white-3;
        }
        p {
            color: $white-6;
        }
    }
}
.blog-sidebar {
    .blog-wrapper {
        .search-bar {
            input {
                background-color: rgba($dark-main, 0.8);
                color: $white-6;
            }
        }
        .sidebar-content {
            .sidebar-list {
                li {
                    border-color: $dark-border;
                    a {
                        color: $dark-link;
                    }
                }
            }
            .tags {
                li {
                    background-color: rgba($dark-main, 0.8);
                    a {
                        color: $dark-link;
                    }
                }
            }
        }
    }
}
.blog-inner {
    &.blog_section {
        .blog-list {
            .blog-wrap {
                .blog-details {
                    h6 {
                        i {
                            color: rgba(255, 255, 255, 0.24);
                        }
                    }
                }
            }
        }
        .blog-wrap {
            .blog-details {
                .link {
                    a {
                        color: rgba(white, 0.74);
                    }
                }
            }
        }
    }
}
.pagination-section {
    .pagination {
        .page-item {
            .page-link {
                background-color: $dark-main;
                box-shadow: none;
                color: $dark-link;
            }
            &.active {
                .page-link {
                    background-color: $theme-color1;
                    color: white;
                }
            }
        }
    }
}
.blog-single-detail {
    .title-part {
        .post-detail {
            li {
                color: $white-6;
                +li {
                    border-color: $dark-border;
                }
            }
        }
    }
    .detail-part {
        p {
            color: $white-6;
        }
    }
    .comment-section {
        .comment-wrapper {
            .comment-box {
                .media {
                    .media-body {
                        .title {
                            border-color: $dark-border;
                            .comment-user,
                            .comment-date {
                                h6 {
                                    color: $white-6;
                                }
                            }
                        }
                        .comment-detail {
                            p {
                                color: $white-6;
                            }
                        }
                        .reply-btn {
                            a {
                                color: $dark-link;
                            }
                        }
                    }
                }
            }
        }
    }
    .leave-comment {
        .form-control {
            background-color: $dark-main;
            color: $white-6;
        }
    }
}
.effect-cls {
    &:after {
        filter: invert(0.83);
    }
}
.portfolio-section {
    .grid {
        &.blog-masonry {
            .grid-item {
                .overlay {
                    .blog-details {
                        h5 {
                            color: $white-5;
                        }
                        h6 {
                            color: $white-6;
                            i {
                                color: rgba(white, 0.24);
                            }
                        }
                        p {
                            color: $white-6;
                        }
                        .link {
                            a {
                                color: $dark-link;
                            }
                        }
                    }
                }
            }
        }
    }
}
.flight-search {
    .flight-search-detail {
        background-color: $dark-body;
        box-shadow: none;
        .form-group {
            .gj-datepicker,
            .gj-timepicker {
                button {
                    border-color: $dark-border !important;
                    background-color: $dark-body;
                }
            }
            img {
                filter: invert(0.8);
            }
            .form-control {
                background-color: $dark-body;
                border-color: $dark-border !important;
                color: $white-6 !important;
            }
        }
    }
    .responsive-detail {
        background-color: $dark-body;
        .destination {
            span {
                color: white;
            }
        }
        .details {
            span {
                color: $dark-span;
            }
        }
    }
}
.shadow-cls {
    box-shadow: 0px 1px 9px 3px rgba($dark-main, 0.81);
}
.filter-panel {
    background-color: $dark-body;
    .left-filter {
        .filters {
            ul {
                li {
                    background-color: $dark-body;
                    &.active {
                        background-color: $theme-color1;
                    }
                }
            }
        }
    }
}
.filter_button {
    background-color: $dark-body;
    img {
        filter: invert(1);
    }
}
.special-section {
    .special-box {
        box-shadow: 5px 8.66px 30px 0px rgba(0, 0, 0, 0.1);
        .special-content {
            h5 {
                color: white;
            }
        }
        .special-img {
            .top-icon {
                i {
                    background-color: rgba(0, 0, 0, 0.4);
                }
            }
            .content-inner {
                background-color: $dark-main;
                h5 {
                    color: $white-5;
                }
                h6 {
                    color: $white-6;
                }
            }
        }
    }
    &.grid-box {
        .special-box {
            .special-content {
                .bottom-section {
                    .rating {
                        span {
                            color: rgba(white, 0.65);
                        }
                    }
                }
                &.restaurant-detail {
                    h5 {
                        span {
                            color: white;
                        }
                    }
                }
            }
            &.cab-box {
                .special-content {
                    background-color: $dark-body;
                }
            }
        }
    }
}
.classic-section {
    .classic-box {
        box-shadow: 2.5px 4.33px 25px 0 rgba(0, 0, 0, 0.2);
        .top-part {
            .plane-updown {
                filter: invert(1);
            }
        }
    }
}
.filter-bottom-content {
    box-shadow: 0px 1px 9px 3px rgba($dark-main, 0.81);
}
.left-sidebar {
    background-color: $dark-body;
    .middle-part {
        .filter-block {
            .collapse-block-title {
                color: white;
                &:after {
                    color: $white-5;
                    background-color: $dark-main;
                }
            }
            .custom-control-label {
                color: rgba(white, 0.7);
                img {
                    filter: invert(0.8);
                }
                &:before {
                    background: $dark-main;
                }
            }
            .collection-collapse-block {
                border-color: $dark-border;
            }
        }
        .section-title {
            border-color: $dark-border;
            h5 {
                color: white;
            }
            img {
                filter: invert(1);
            }
        }
    }
    .search-bar {
        border-color: $dark-border;
        ::placeholder {
            color: rgba(white, 0.5);
        }
        input {
            background-color: $dark-main;
            color: $white-5;
        }
    }
    .bottom-info {
        h6 {
            color: rgba(white, 0.7);
        }
    }
    .back-btn {
        border-color: $dark-border;
        color: $white-6;
    }
    &.sidebar-popup {
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.38);
        border: none;
    }
}
.mobile-filter {
    background-color: $dark-body;
    img {
        filter: invert(1);
    }
}
.flight-detail-sec {
    .title-bar {
        p {
            color: $white-6;
        }
    }
    .detail-bar {
        .detail-wrap {
            background-color: $dark-body;
            .airport-part {
                .airport-name {
                    h4 {
                        color: $white-4;
                    }
                    h6 {
                        color: $white-6;
                    }
                }
                .airport-progress {
                    i {
                        color: rgba(white, 0.17);
                    }
                }
            }
        }
    }
    &.cab-detail-sec {
        .detail-bar {
            .detail-wrap {
                .car-details {
                    ul {
                        li {
                            color: $white-6;
                            img {
                                filter: invert(1);
                            }
                        }
                    }
                }
                .price {
                    h6 {
                        color: $white-6;
                        span {
                            color: white;
                        }
                    }
                }
            }
        }
    }
}
.detail-section {
    &.no-bg-detail {
        .detail-box {
            background-color: $dark-main;
            background-image: none;
            .upper-part {
                h6,
                h2,
                h5 {
                    color: white;
                }
            }
            &:hover {
                background-image: url(../images/flights/sky2.jpg);
            }
        }
    }
}
.flight-detail {
    .flight-box {
        background-color: $dark-main;
        .middle-section {
            .flight-info {
                h4 {
                    color: $white-4;
                }
                &:after {
                    background-color: $dark-border;
                }
            }
        }
        .name-section {
            h5 {
                color: $white-5;
            }
        }
    }
}
.review-section {
    .review_box {
        box-shadow: 0px 1px 9px 3px rgba($dark-main, 0.81);
        background-color: $dark-body;
        .title-top {
            background-color: $dark-main;
        }
        .flight_detail {
            .promo-section {
                .form-control {
                    background-color: $dark-body;
                    border-color: $dark-border !important;
                    color: $white-6 !important;
                }
            }
            .summery_box {
                .grand_total {
                    background-color: $dark-main;
                }
            }
            &.payment-gateway {
                .card {
                    border-color: $dark-border;
                    .card-header {
                        background-color: $dark-main;
                        .btn {
                            label {
                                color: rgba(255, 255, 255, 0.7);
                            }
                        }
                    }
                }
                form {
                    .form-control {
                        border-color: rgba($dark-border, 0.75);
                    }
                }
            }
        }
    }
}
.category-bg {
    .category-block {
        background-color: $dark-body;
        h6 {
            background-color: $dark-body;
            color: $white-6;
        }
        h5 {
            color: $white-5;
        }
    }
}
.ticket-section {
    .ticket-box {
        .ticket-title {
            h6 {
                background-color: $dark-body;
            }
        }
        .content {
            background-color: $dark-body;
            &:before,
            &:after {
                filter: invert(0.84);
            }
            .detail {
                h4 {
                    color: $white-4;
                }
                h3 {
                    color: $white-3;
                }
                p {
                    color: $white-6;
                }
            }
            .barcode-design {
                img {
                    filter: invert(1);
                }
            }
        }
    }
    .slick-prev,
    .slick-next {
        &:before {
            background-color: $dark-body;
        }
    }
}
.guest-detail {
    background-color: $dark-body;
    box-shadow: 0 2px 4px 0 $dark-main;
    form {
        .form-control {
            background-color: $dark-body;
            border-color: $dark-border !important;
            color: $white-6 !important;
        }
    }
}
.deals {
    .deals-box {
        .left-portion {
            background-color: $dark-body;
            &:before {
                filter: invert(0.84);
            }
            .deals-content {
                img {
                    filter: invert(1);
                }
                .detail {
                    h2 {
                        span {
                            color: white;
                        }
                    }
                }
            }
        }
    }
}
.input-group-text {
    background-color: $dark-main;
    border-color: $dark-border;
    color: $dark-link;
}
.card {
    background-color: $dark-body;
    border-color: $dark-border;
}
.card-header {
    background-color: rgba($dark-main, 0.03);
    border-color: $dark-border;
}
.book-panel {
    background-color: $dark-body;
    box-shadow: -1px -2px 6px 0px rgba($dark-body, 0.3);
    .text {
        background-color: $dark-body;
        border-color: $dark-border;
        color: $dark-link;
    }
    .btn {
        background-color: $dark-body;
        color: $dark-link;
        &.theme-color {
            border: none;
        }
    }
}
.blog-section {
    .blog-box {
        .blog-content {
            background-color: $dark-body;
            p {
                color: white;
            }
            h6 {
                color: $white-6;
            }
        }
    }
}
.cab-full {
    .cab-search-section {
        .cab-search-box {
            background-color: $dark-main;
        }
    }
}
.text-dark {
