        }
        ul {
            li {
                &:after {
                    background-color: white;
                }
            }
        }
    }
}
.onclick-map {
    .book-table {
        &.single-table {
            .table-form {
                &.classic-form {
                    background-color: $dark-body;
                    form {
                        .form-group {
                            input {
                                background-color: $dark-main;
                            }
                            img {
                                filter: invert(1);
                            }
                        }
                    }
                }
                form {
                    .form-control {
                        background-color: $dark-main;
                    }
                }
            }
        }
    }
}
.book-table {
    &.single-table {
        .table-form {
            background-color: $dark-body;
            &:after,
            &:before {
                background-color: $dark-body;
            }
            &.classic-form {
                background-color: $dark-body;
                form {
                    .form-group {
                        input {
                            background-color: $dark-main;
                        }
                        img {
                            filter: invert(1);
                        }
                    }
                }
            }
            form {
                .form-control {
                    background-color: $dark-main;
                }
            }
        }
    }
}
.effect-cls-up {
    &:before {
        filter: invert(0.83);
    }
}

.full-banner {
    .tourSection {
        .title-3 {
            h2 {
                color: black;
            }
        }
        .tourBox {
            .tourContent {
                h5,
                h6 {
                    color: black;
                }
            }
        }
    }
}

.full-width-detail {
    .about-section {
        .about-text {
            .right-detail {
                .bottom-detail {
                    p {
                        color: rgba(white, 0.7);
                    }
                }
            }
        }
    }
    .place-detail {
        li {
            color: white;
        }
    }
}
.typography_section {
    code {
        background-color: #383838;
    }
    .typography-box {
        .typo-content {
            .sub-title {
                border-color: rgba(white, 0.1);
                color: $white-6;
            }
            &.heading_content {
                h1,
                h2,
                h3,
                h4,
                h5,
                h6 {
                    color: white;
                }
            }
        }
        .headings {
            background-color: $dark-main;
            border-color: $dark-border;
            h3 {
                color: $white-3;
            }
        }
    }
}
.dashboard-section {
    .dashboard-main {
        .dashboard-intro {
            background-color: $dark-main;
            .complete-profile {
                .complete-box {
                    background-color: $dark-body;
                    box-shadow: 0 2px 4px 0 rgba(68, 68, 68, 0.22);
                }
            }
            p {
                color: rgba(white, 0.8);
            }
        }
        .counter-section {
            .counter-box {
                background-color: $dark-main;
                h5 {
                    color: rgba(white, 0.6);
                }
            }
        }
        .dashboard-info {
            #chart,
            .chart-box {
                background: $dark-main;
            }
            .activity-box {
                background: $dark-main;
                ul {
                    li {
                        background-color: $dark-body;
                    }
                }
            }
        }
    }
    .dashboard-sidebar {
        background-color: $dark-body;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.27);
        .profile-top {
            .profile-detail {
                h6 {
                    color: $white-6;
                }
            }
        }
    }
    .dashboard-box {
        .dashboard-detail {
            ul {
                li {
                    .details {
                        .left {
                            h6 {
                                color: rgba($white-6, 0.6);
                            }
                        }
                    }
                }
            }
            .booking-box {
                background-color: $dark-main;
                box-shadow: -1px 2px 8px #1d1d1d;
                .detail-middle {
                    .media {
                        .media-body {
                            h6 {
                                color: $white-6;
                            }
                            p {
                                color: rgba(white, 0.6);
                                span {
                                    color: #d6d6d6;
                                }
                            }
                        }
                        .icon {
                            i {
                                background-color: $dark-body;
                                box-shadow: -1px 2px 8px #1d1d1d;
                            }
                        }
                    }
                }
                .detail-last {
                    i {
                        color: white;
                    }
                }
                .date-box {
                    span {
                        &.date {
                            color: white;
                        }
                    }
                }
            }
            .card-payment {
                .payment-card {
                    &.add-card {
                        background-color: $dark-body;
                        background-image: repeating-linear-gradient(45deg, rgba(black, 0) 1px, rgba(black, 0.03) 2px, rgba(black, 0.04) 3px, rgba(black, 0.05) 4px), -webkit-linear-gradient(-245deg, rgba(black, 0) 40%, rgba(black, 0.2) 70%, rgba(black, 0) 90%);
                    }
                }
            }
            .delete-section {
                p {
                    color: rgba(white, 0.64);
                    .text-bold {
                        color: rgba(white, 0.8);
                    }
                }
            }
        }
    }
}
.close {
    color: white;
}
.faq-tab {
    .nav-tabs {
        .nav-item {
            .nav-link {
                background-color: transparent;
            }
        }
    }
}
}

//responsive start
@media (max-width: 1199px) {
    .dark {
        header {
            .main-navbar {
                .nav-menu {
                    background-color: $dark-body;
                    border-color: $dark-border;
                    .back-btn {
                        .mobile-back {
                            color: $dark-link;
                            border-color: $dark-border;
                        }
                    }
                    >li {
                        a {
                            color: $dark-link;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .dark {
        .coming-soon {
            &.travel {
                .coming-soon-detail {
                    background-color: rgba(black, 0.6);
                }
            }
        }
    }
}

@media (max-width: 991px) {
    .dark {
        .flight-search {
            .flight-search-detail {
                background-color: rgba(0, 0, 0, 0.65);
                form {
                    background-color: $dark-main;
                }
            }
        }
        .home_section {
            .home {
                .home-content {
                    .flight-search {
                        .flight-search-detail {
                            background-color: $dark-main;
                        }
                    }
                }
            }
        }
        .cab-full {
            .flight-search {
                .flight-search-detail {
                    background-color: $dark-main;
                }
            }
        }
        .top-filter-section {
            ul {
                .onclick-title,
                .filter-title {
                    .onclick-content {
                        box-shadow: none;
                        .custom-control {
                            .custom-control-label {
                                img {
                                    filter: invert(1);
                                }
                            }
                        }
                    }
                }
                .back-btn {
                    color: white;
                    border-color: $dark-border;
                }
            }
        }
        .mobile-filter {
            border-color: $dark-border;
        }
        .single-section {
            .single-sidebar {
                &.order-cart-right {
                    background-color: $dark-body;
                    box-shadow: none;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .dark {
        .filter-panel {
            .filter-bottom-title {
                border-color: $dark-border;
            }
            .right-panel {
                &.search_filter {
                    border-color: $dark-border;
                }
            }
        }
        .dashboard-section {
            .dashboard-box {
                .dashboard-detail {
                    .card-payment {
                        .edit-card-mobile {
                            a {
                                color: white;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 576px) {
    .dark {
        .filter-panel {
            .respon-filter-btn {
                h6 {
                    color: white;
                }
            }
            .respon-filter-content {
                &.show {
                    background-color: $dark-body;
                    box-shadow: -4px -1px 20px 0px rgba($dark-main, 0.13);
                    ul {
                        li {
                            &.active {
                                background-color: $dark-body;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 420px) {
    .dark {
        .single-section {
            .description-section {
                .description-details {
                    .menu-part {
                        .rooms-box {
                            .price-details {
                                border-color: $dark-border;
                            }
                        }
                    }
                }
            }
        }
    }
}

